<template>
  <div class="mt-15">
    <app-bar :showSearch="false" />
    <div class="d-flex align-center">
      <v-img
        src="https://images.unsplash.com/photo-1617286647476-3d627ea28df4?crop=entropy&cs=tinysrgb&fm=jpg&ixlib=rb-1.2.1&q=60&raw_url=true&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MjV8fHNob3B8ZW58MHwyfDB8fA%3D%3D&auto=format&fit=crop&w=600"
        max-height="60px"
        max-width="60px"
      ></v-img>
      <h2>ＪＪ虹吸咖啡</h2>
    </div>
    <h3 class="mt-2">
      {{ detail.name }}
    </h3>
    <!-- <div class="d-flex justify-space-between mb-4">
      <span>
        {{ detail.date }}
      </span>
      <div>
        <v-icon class="mr-2">share</v-icon>
        <span title="我的最愛" @click="favStatus">
          <v-icon v-if="!isFav">favorite_border</v-icon>
          <v-icon v-else>favorite</v-icon>
        </span>
      </div>
    </div> -->
    <p>
      {{ detail.detail }}
    </p>
    <v-img
      src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
      height="auto"
      width="100%"
    ></v-img>

    <template>
      <v-simple-table class="mt-5">
        <template v-slot:default>
          <tbody>
            <!-- <tr>
              <td style="min-width: 105px">展覽資訊：</td>
              <td class="pl-0">2022台北國際嬰兒與孕媽咪用品展</td>
            </tr>
            <tr>
              <td style="min-width: 105px">展出日期：</td>
              <td class="pl-0">2022年10月07日(五)－10月10日(一)</td>
            </tr>
            <tr>
              <td style="min-width: 105px">展出時間：</td>
              <td class="pl-0">10:00~18:00</td>
            </tr> -->
            <tr>
              <td style="min-width: 105px">展出地點：</td>
              <td class="pl-0 d-flex justify-space-between align-center">
                <span> 台北世貿一館 | 110台北市信義區信義路五段5號 </span>
                <v-icon>pin_drop</v-icon>
              </td>
            </tr>
            <tr>
              <td style="min-width: 105px">聯絡方式：</td>
              <td class="pl-0 d-flex justify-space-between align-center">
                <span> {{ detail.phone }} </span>
                <v-icon>phone_in_talk</v-icon>
              </td>
            </tr>
            <tr>
              <td style="min-width: 105px">網址：</td>
              <td class="pl-0 d-flex justify-space-between align-center">
                <span> {{ detail.website }} </span>
                <v-icon @click="openInNew(detail.website)">open_in_new</v-icon>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
    </template>
    <v-row>
      <v-col cols="6">
        <v-btn
          outlined
          small
          @click="favStatus"
          class="my-3"
          style="width: 100%"
        >
          <v-icon size="12">favorite_border</v-icon>
          {{ dialogText }}
        </v-btn>
      </v-col>
      <v-col cols="6">
        <v-btn
          outlined
          small
          @click="favStatus"
          class="my-3"
          style="width: 100%"
        >
          <v-icon size="12">share</v-icon>
          分享店家
        </v-btn>
      </v-col>
    </v-row>

    <v-img src="../assets/googlemap.jpg" height="auto" width="100%"></v-img>
  </div>
</template>

<script>
import AppBar from "../components/AppBar";

export default {
  components: {
    AppBar,
  },

  data: () => ({
    detail: {
      date: "04/01 ~ 04/04",
      detail:
        "10/07-10台北世貿一館！全球百大精品孕嬰領導品牌、破萬件婦幼夯品強勢出擊！廣蒐全球最新、詢問度最高之人氣婦嬰商品，並邀請各路婦幼翹楚與育兒專家共同分享孕期與育兒知識，更有多款優質明星商品折扣優惠，展場獨家超值限定商品！展覽商品包含孕婦服裝、孕哺衣、哺乳枕、媽媽包、保養品、營養補給品、塑身衣、彌月蛋糕/油飯/餅乾、彌月禮品、嬰兒食品、營養補充、玩具、推車、安全坐椅、幼兒衛浴、嬰兒床寢、各式教材器具、兒童圖書等，所有商品應有盡有，展覽現場更有限量福袋免費領、萬元推車一元抽、上聯獨家優惠商品、寶寶運動會等活動!",
      name: "孕媽咪與寶貝活動",
      phone: "886-2-27597167",
      website: "https://www.google.com.tw/?hl=zh_TW",
    },
    isFav: false,
    dialogText: "",
  }),

  // created() {
  //   if (!localStorage.getItem("verifyData")) {
  //     this.$router.push("/login");
  //   }
  // },

  mounted() {
    // this.detail = JSON.parse(localStorage.getItem("toDetail"));
    let data = JSON.parse(localStorage.getItem("favList"));
    let findIndex = -1;
    if (data) {
      data.findIndex((e) => e.phone === this.detail.phone);
    }
    this.isFav = findIndex == -1 ? false : true;
    this.dialogText = findIndex == -1 ? "加到最愛" : "移除最愛";
  },

  methods: {
    favStatus() {
      this.isFav = !this.isFav;
      if (this.isFav) {
        let data = null;
        if (localStorage.getItem("favList")) {
          data = JSON.parse(localStorage.getItem("favList"));
          data.push(this.detail);
        } else {
          data = [this.detail];
        }
        localStorage.setItem("favList", JSON.stringify(data));
        this.dialogText = "移除最愛";
      } else {
        let data = JSON.parse(localStorage.getItem("favList"));
        let findIndex = data.findIndex((e) => e.phone === this.detail.phone);
        data.splice(findIndex, 1);
        localStorage.setItem("favList", JSON.stringify(data));
        this.dialogText = "加到最愛";
      }
    },
    openInNew(url) {
      window.open(url);
    },
  },
};
</script>
